import React from "react";

import { Tabs, TabsPanel, Spinner } from "@salesforce/design-system-react";
import GRAXSummaryChartsAdvanced, { RECORD_SUMMARIES_VARIANT } from "./GRAXSummaryChartsAdvanced";

export default class GRAXSummaryCharts extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isLoading: true,
    };
  }

  componentDidMount() {
    this.setState({
      isLoading: false,
    });
  }

  getFilterParams() {
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);

    const fromDate = urlParams.has("fromDate")
      ? new Date(parseInt(urlParams.get("fromDate")))
      : undefined;
    const toDate = urlParams.has("toDate")
      ? new Date(parseInt(urlParams.get("toDate")))
      : undefined;
    const gspId = urlParams.has("gspId") ? urlParams.get("gspId") : undefined;

    return {
      fromDate,
      toDate,
      gspId,
    };
  }

  render() {
    if (this.state.isLoading) {
      return <Spinner size="large" variant="brand" />;
    }

    return (
      <div style={{ minHeight: 1000 }}>
        <GRAXSummaryChartsAdvanced
          variant={RECORD_SUMMARIES_VARIANT}
          filterParams={this.getFilterParams()}
        />
      </div>
    );
  }
}
